














































.spin-block {
  .form-control.focus {
    box-shadow: none !important;
    border: 0 !important;
  }
  .spin-button {
    border: 0;
    height: auto;
    &:focus {
      box-shadow: none;
    }

    #demo-sb {
      border-left: 10px solid;
      background: white;
      font-weight: bold;
      bdi {
        font-size: 20px;
        color: black;
      }
    }
    .border-left {
      border-left: 0 !important;
    }
    .border-right {
      border-right: 0 !important;
    }

    .decrement {
      margin: 8px 0;
      cursor: pointer;
      user-select: none;
      color: #49a0a2;
      width: 42px;
      height: 42px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      box-shadow: 0 4px 10px #f7f4f4;
      border: 1px solid #f4f3f3;
      .moins {
        width: 22px;
        height: 22px;
        border: 2px solid #49a0a2;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        font-family: Ubuntu;
      }
    }
  }
}
